import React from 'react';
import ReactDOM from 'react-dom';

class ProductTable extends React.Component
{
 constructor(props)
 {
  super(props);
  this.state = {szukaj: '', box: false, cart: [], cart2: [], total: 0, viewMode: false,viewOrder: false,viewProduct: '',fullName: '', email:'',

  phone: '', adress: '', message: ''

		};  

  this.handleZmiana = this.handleZmiana.bind(this);
  this.handleBox = this.handleBox.bind(this);
  this.handleDodaj = this.handleDodaj.bind(this);
  this.handleDelete = this.handleDelete.bind(this);

  this.handleViewMode = this.handleViewMode.bind(this);
  this.handleViewOrder = this.handleViewOrder.bind(this);

  this.cancelOrder = this.cancelOrder.bind(this);
  this.sendOrder = this.sendOrder.bind(this);

  this.updateFullName = this.updateFullName.bind(this);
  this.updateEmail = this.updateEmail.bind(this);
  this.updatePhone = this.updatePhone.bind(this);
  this.updateAdress = this.updateAdress.bind(this);

 }

  updateAdress(pro)
  {
   this.setState({adress: pro.target.value})
  }

  updatePhone(pro)
  {
   this.setState({phone: pro.target.value})
  }

  updateEmail(pro)
  {
   this.setState({email: pro.target.value}) 
  }
 
  updateFullName(pro)
  {
   this.setState({fullName: pro.target.value});
  }
 
  sendOrder()
  {
   if(this.state.fullName =='' || this.state.email =='' || this.state.phone == '' || this.state.adress == '')
   {
    alert('Please enter all your personal information');
   } else {
	
	   alert('Order was sent')           

	   const wiadomosc= <div> Full name: {this.state.fullName}<br></br>
			email: {this.state.email}<br></br>
			Phone: {this.state.phone}<br></br>
			Adress: {this.state.adress}<br></br>
			{this.state.cart2.map((rzecz)=><div>{rzecz.name} {rzecz.price}</div>)}
			Total sum: ${this.state.total.toFixed(2)}
			</div>;

           this.setState({message: wiadomosc});

	   

          };
  }

  cancelOrder()
  {
  this.setState({viewOrder: !this.state.viewOrder});
  }

  handleViewOrder(pro)
  {
  	const om=this.state.viewOrder;
	this.setState({viewOrder: !om});
  }

  handleViewMode(pro)
  { 
   const vm=this.state.viewMode;
   this.setState({viewMode: !vm}); 

    this.props.products.forEach((product)=>{
	
        if(product.id==pro.target.value){
         this.setState({viewProduct: product});
        }

    })

  }

  handleDelete(pro)
  {

   const el=pro.target.value;
   
   let l= this.state.cart;
   

   l[el]='';


	this.setState({cart: l});
   
   const ll=this.state.cart2;
   
   const odejmij=ll[el].price;

   ll[el]='';

   this.setState({cart2: ll});
   
   this.setState({total: this.state.total-odejmij});   
   
  }

  handleDodaj(pro)
  {
   




   const lista= this.state.cart;
   
   const lista2=this.state.cart2;

   var q=0;

   this.props.products.forEach((product)=>{
	
        if(product.id==pro.target.value){ 
	q=lista.length;
			lista.push(<li>{product.name} ${product.price} <button value={q} onClick={this.handleDelete}>Remove</button></li>);
			this.setState({total: this.state.total+parseFloat(product.price)});
			
			lista2.push(product);
			
									 }})






   //lista.push(<li>{pro.target.value}</li>);
   this.setState({cart: lista});   
 
   this.setState({cart2:lista2});

  }

  handleZmiana(tekst)
  {
  this.setState({szukaj: tekst.target.value});
  }  

  handleBox(tekst)
  {
  this.setState({box: !this.state.box});
  }

  render()
  {

  const products=this.props.products;  

  const cartrows = this.state.cart;

  const rows = [];
  rows.push(products.map(
   (product)=><li>{product.name} ${product.price} {product.stocked ? 'Available':'N/A'}</li>)
  );

  const rows2 = [];
  
 this.props.products.forEach((product)=>{
   
if(this.state.box==false){
   if(product.name.indexOf(this.state.szukaj) === -1) {
    return;
   } else {rows2.push(<li>{product.name} ${product.price} {product.stocked ? 

<div>
 <button value={product.id} onClick={this.handleDodaj}>Add</button>
 <button value={product.id} onClick={this.handleViewMode}>View</button>
</div> :

<div>
N/A&nbsp;&nbsp;&nbsp;&nbsp;
<button value={product.id} onClick={this.handleViewMode}>View</button>
</div>
}


      
      
</li>);}


} else {

   if(product.name.indexOf(this.state.szukaj) === -1 || product.stocked == false) {
    return;
   } else {rows2.push(<li>{product.name} ${product.price} 
      <div>
      <button value={product.id} onClick={this.handleDodaj}>Add</button>
      <button value={product.id} onClick={this.handleViewMode}>View</button>
      </div>
   </li>);}
}

   }
  );
   

   
 var vmnapis=''   
   if(this.state.viewMode==true)
   {
    vmnapis = <div>
               <fieldset>
		<legend>View product</legend>
	        	<h3>{this.state.viewProduct.name} price: ${this.state.viewProduct.price}                {this.state.viewProduct.stocked ? '':'Not available'}</h3>

               </fieldset>
	       <fieldset>
		<legend>Product Description:</legend>
			<div>
			<h3>{this.state.viewProduct.description}</h3>
			</div>
	       </fieldset>
	

               {this.state.viewProduct.stocked ? 
			<div>
				<button value={this.state.viewProduct.id} onClick={this.handleDodaj}>Add to Cart</button>
	                        <button onClick={this.handleViewMode}>Back to Shopping</button>
	
			</div>
		 :      <div>
	                        <button onClick={this.handleViewMode}>Back to Shopping</button>
			</div>
		}
              </div>;
   
   }

 var reszta2=''
if(this.state.viewOrder==true)
{

 const listazakupow = this.state.cart2.map((rzecz)=>
						    
							<div>
								{rzecz.name} {rzecz.price}
							</div>);
						     

 reszta2= <div>
          	<fieldset>
		<h3>Your order</h3>
		{listazakupow}
		
		<h4>Total sum: ${this.state.total.toFixed(2)}</h4>
		</fieldset>

        	<fieldset>
		Your delivery information<br></br><br></br>
			Full Name:<input type="text" value={this.state.fullName} onChange={this.updateFullName} /><br></br>
			e-mail:<input type="text" value={this.state.email} onChange={this.updateEmail}/><br></br>
			Phone:<input type="text" value={this.state.phone} onChange={this.updatePhone}/><br></br>
			Adress:<input type="text" value={this.state.adress} onChange={this.updateAdress}/><br></br>
			<button onClick={this.sendOrder}>Send order.</button>
                        <button onClick={this.cancelOrder}>Cancel order.</button>
          	</fieldset>
        	<fieldset>
			{this.state.message}
        	</fieldset>
          </div>

}

 var reszta=''
   if(this.state.viewMode==false && this.state.viewOrder==false)
   {
reszta=<div>

	Mateusz Pawlowski schopping cart demo application.

	<fieldset>
	<legend>Search</legend>
	<input type="text" placeholder="Search..." value={this.state.szukaj} onChange={this.handleZmiana}/>
        <div>Available<input type="checkbox" checked={this.state.box} onChange={this.handleBox}/></div>
        
	</fieldset>

	<fieldset>
        <legend>Shopping Cart</legend>
         <ul>{cartrows}</ul>
         <h3>Total sum: ${this.state.total.toFixed(2)} 	      
		
		{this.state.total>0.9 ? <button onClick={this.handleViewOrder}>Make order</button> : ''}
</h3>
        </fieldset>

	<fieldset>
        <legend>Product List filtered</legend>
         <ul>{rows2}</ul>
        </fieldset>
	<fieldset>
        <legend>Product List</legend>
         <ul>{rows}</ul>
        </fieldset>
</div>


   }

  return(
     <div>
 	{vmnapis}
	{reszta}
	{reszta2}
     </div>
   );




  
  };
  
}


const PRODUCTS = [
{category: 'Sporting Goods', price: '49.99', description:'Football Description...',stocked:true, name: 'Football', id:'1'},
{category: 'Sporting Goods', price: '9.99', description:'Baseball Description...', stocked:true, name: 'Baseball', id:'2'},
{category: 'Sporting Goods', price: '29.99',description:'Basketball Description...', stocked:false, name: 'Basketball', id:'3'},
{category: 'Electronics', price: '99.99', description:'iPod Touch Description...',stocked:true, name: 'iPod Touch', id:'4'},
{category: 'Electronics', price: '399.99',description:'iPhone 5 Description...', stocked:false, name: 'iPhone 5', id:'5'},
{category: 'Electronics', price: '199.99', description:'Nexus 7 Description...',stocked:true, name: 'Nexus 7', id:'6'}
];

ReactDOM.render(<ProductTable products={PRODUCTS} />, document.getElementById('root'));